@import 'src/style.module.scss';
.MemberCard{
  
  &-img{
    position: relative;
    z-index: 1;
    width: 200px;
    height: 200px;
    img{
      width: 100%;
      height: 100%;
      position: relative;
      border-radius: 50%;
      margin: 0 auto;
      border: 10px solid #eeeeef;
    }
  }
  &-text{
    background-color:white;
    color: #040a36;
    padding: 110px 30px 30px 30px;
    height: 265px;
    border-radius: 4px;
    margin-top: -90px;
    cursor: pointer;
    position: relative;
    transition: 0.4s;
    z-index:0;
    .memberName{
      font-size: 20px;
      font-weight: 600;
      margin-bottom: 8px;
    }
    .profession{
      font-size: 14px;
      font-weight: 500;
      margin-bottom: 10px;
    }
    .social-icons{
      @include Dflex(center);
      padding: 0;

      li a{
        @include Dflex(center);
        width: 34px;
        height: 34px;
        background: #040a36;
        color: #fff;
        border-radius: 50%;
        margin-right: 5px;
        transition: 0.4s;
        &:hover{
          transform: scale(1.3);
        }
      }
    }
  }
  &:hover  &-text {
    background-color: #040a36;
    color: white;
  }
}