@import url('https://fonts.googleapis.com/css2?family=Lobster&family=Poppins:wght@300;400;500;600;700&display=swap');

$bgcolor:#040a36;
$NavRightMargin:30px;
$Section-Padding:70px;
*{
  box-sizing:border-box;
  list-style:none;
  text-decoration:none;
  font-family: 'Poppins', sans-serif;
  scroll-behavior: smooth;
}
body{
  background-color:#eeeeef;
  margin: 0;
}
// body{
//   background: linear-gradient(to bottom,#ff4495 0%,#ff6d6d 100%);
// }
@mixin Dflex($jcontent) {
  display: flex;
  justify-content: $jcontent;
  align-items: center;
  flex-wrap: wrap;
}

@mixin upper-title{
  font-size: 26px;
  color:#fcd307;
  font-weight:600;
  margin-bottom: 20px;
  position: relative;
  &::before{
    content: "";
    position: absolute;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
    width: 200px;
    height: 2px;
    background-color: #fcd307;
  }
}
@mixin Title {
  font-size: 46px;
  letter-spacing: 2px !important;
  color: #081158;
  font-weight: 600;
  margin-bottom: 25px;
}


@media (max-width:375px){
  .Section-bottom-Title{
   font-size: 30px !important;
  }
}