.Card{
  perspective: 1000px;
  margin-right: 40px;
  .Service-Card{
    min-height: 420px;
    border-radius: 15px !important;
    background-color: white;
    position: relative;
    cursor: pointer;
    transition: transform 0.6s !important;
    transform-style: preserve-3d;
    color: #081158;
      .Card-front{
        position:absolute;
        top: 30px;
        left: 0;
        height: 100%;
        width: 100%;
        backface-visibility: hidden;
        img{
          width: 120px;
          margin:40px 0 20px 0 ;
        }
      }
      .Card-back{
        position:absolute;
        top: 0;
        left: 0;
        height: 100%;
        width: 100%;
        border-radius: 15px;
        transform: rotateY(180deg);
        backface-visibility: hidden;
        padding:30px 20px;
        background-color: #fcd307;
      }
  }
  &:hover .Service-Card{
    transform: rotateY(180deg);
  }
}

@media (max-width:1024px) {
  .Service-Card{
    .Card-back{
      .Card-text{
        font-size: 16px;
      }
    }
  }
}
@media( max-width:768px){
  .Card{
    margin-right: 0 ;
  }
  .Service-Card{
    width: 100%;

    .Card-back{
      .Card-text{
        font-size: 18px !important;
      }
    }
  }
}