.BlogCard{
  display: inline-block;
  height: 500px;
  position: relative;
  overflow: hidden;
  border-radius: 15px;
  position: relative;
  &::before{
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    height:100%;
    width: 100%;
    background-color: rgba(0, 0, 0, 0.636);
  }
  &-img{
    height:100%;
    img{
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
  &-text{
    position: absolute;
    bottom: 0;
    color: white;
    height: 95%;
    padding:10px;
    transform: translateY(75%);
    transition: 0.4s;
    .shownText{
      margin-bottom: 40px;
    }
    
  }

  &:hover .BlogCard-text{
    transform: translateY(0) !important;
  }
}

@media( max-width:1024px){
  .BlogCard:hover .BlogCard-text{
    transform: translateY(-5%) !important;
    .shownText{
      margin-bottom: 10px;
    }
  }
}
@media( max-width:768px){
  .BlogCard:hover .BlogCard-text{
    transform: translateY(0%) !important;
  }
  .shownText{
    margin-bottom: 20px;
  }
}

@media( max-width:768px){
  .BlogCard-text{
   font-size: 14.5px;

   .shownText{
    font-size: 24px;
    margin-bottom: 40px;
   }
  }
}