@import 'src/style.module.scss';

.header{
  background-color: $bgcolor !important;
  @include Dflex(center);

  .logo{
    font-family: 'Lobster', cursive;
    color: #fcd307;
    font-size: 40px;
  }
  .Navs{

    @include Dflex(center);

    .nav-link{
    
      a{
      display: inline-block;
      margin-left:$NavRightMargin;
      font-size: 14px;
      color: white;
      cursor: pointer;
      position: relative;
      z-index: 0;
      font-weight: 600;

      &::before{
        content: "";
        position: absolute;
        bottom: 0;
        left: 0;
        background-color: #fcd307;
        border: 1px solid rgb(93, 253, 93) ;
        width: 110%;
        height: 100%;
        transform: scaleY(0);
        transform-origin: bottom;
        transition: 0.4s;
        z-index: -1
      }

      &:hover::before{
        transform: scaleY(1) skew(15deg) !important;
      }

      &:hover{
        border: 0 !important;
        color: black !important;
       }

    &.active{
      color:#fcd307;
      font-weight: 700;
      position: relative;

      &::after{
        content: "";
        position: absolute;
        bottom: -2px;
        left: 0;
        background-color: #fcd307;
        width: 113.6%;
        height: 2px;
        z-index: -1
      }
      }

    }
  }
  
  }
}
.menu{
  display: none !important;
}

.MenuUl{
  a.active{
    color:#fcd307;
    font-weight: 700;
    position: relative;
    svg{
      color: #fcd307;
    }
    &::after{
      content: "";
      position: absolute;
      bottom: -2px;
      left: 0;
      background-color: #fcd307;
      width: 113.6%;
      height: 2px;
      z-index: -1
    }
    }
}
@media(max-width:426px){
  .Navs{
    display: none !important;
  }
  .menu{
    display: block !important;
  }
}
