@import 'src/style.module.scss';

.About{
  height: 100vh;
  position: relative;
  @include Dflex(center);
  margin-bottom: 30px;
  z-index: 0;
  &::before{
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.384);
    z-index:-1;
  }
  .heroVideo{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    z-index: -2;
  }
  .heroText-container{
    position: relative;
    width: 40%;
    .hero-text{
      color: white;
    }
    .Marked{
      color: #fcd307;
      font-weight:700;
    }
    .mouseAnimation{
      width: 25px;
      height: 45px;
      border: 2px solid white;
      border-radius: 15px;
      position: absolute;
      left: 50%;
      transform: translate(-50%,40px);
      text-align: center;

      .mouse-wheel{
        display: inline-block;
        width: 4px;
        height: 4px;
        background-color: white;
        border-radius: 50%;
        margin-inline: auto;
        animation: 1.6s ease infinite wheel;
      }
      @keyframes wheel{
        0% {
          transform: translateY(-2px);
          opacity: 0;
      }
      30% {
          opacity: 1;
      }
      100% {
        transform: translateY(15px);
          opacity: 0;
      }
      }
    }
  }
}

@media(max-width:768px){
  .heroText-container{
    width: 80% !important;
  }
}
@media (max-width:426px){
 .heroText-container{
   width: 90% !important;
   .hero-text{
    font-size: 30px !important;
  }
 }
}